@include font($myri, 'MyriadProRegular');
.wrapper {
 }	// min-width: 1100px

.container {
	max-width: 1170px;
	height: 100%;
	margin: 0 auto;
	position: relative;
	@include clr;
	@include r(1210) {
		margin: 0 20px; } }


.section {
	max-width: 1920px;
	margin: 0 auto;
	padding-bottom: 160px;
	position: relative;
	background-repeat: no-repeat;
	background-position: center;

	&_title {
		font-size: rem(190);
		font-weight: 600;
		color: black;
		line-height: 100%;
		opacity: 0.03;
		text-transform: uppercase;
		text-align: center;
		cursor: default; } }

.title-product {
	font-size: 127px;
	position: absolute;
	bottom: 245px;
	right: -245px;
	transform: rotate(-90deg);
	z-index: 0;
	cursor: default; }

.hero {
	background: #fff;
	background-repeat: no-repeat;
	padding-bottom: 0;

	.container {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		min-height: 100vh;
		padding: 20px 0; }

	.header {
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: center;
		font-family: $mons;
		@include r(1024) {
			flex-wrap: wrap;
			justify-content: center; }

		.logo {
			@include r(1024) {
				flex-basis: 50%;
				order: 1; }
			@include r(768) {
				flex-basis: 100%;
				text-align: center;
				margin-bottom: 15px; }
			.icon {
				fill: #000;
				font-size: 28px; } }

		.nav {
			flex-basis: 60%;
			line-height: 100%;
			@include r(1024) {
				flex-basis: 100%;
				margin: 15px 0;
				order: 3; }

			&_list {
				display: flex;
				flex-basis: 100%;
				justify-content: space-between;
				align-items: center;
				@include r(1024) {
					flex-wrap: wrap; }
				@include r(768) {
					flex-direction: column; } }

			&_item {
				flex-basis: auto;
				margin: 10px 0;
				font-weight: 600;
				text-align: center;
				text-transform: uppercase;
				cursor: pointer; }

			&_href {
				display: inline-block;
				padding: 5px;
				color: black;
				text-decoration: none;
				&:hover {
					color: #fff;
					background: $blue; } } }

		&_social {
			@include r(1024) {
				display: flex;
				flex-basis: 50%;
				justify-content: flex-end;
				order: 2; }
			@include r(768) {
				flex-basis: 100%;
				justify-content: center; }

			.icon {
				font-size: 36px;
				fill: #000;
				color: #fff;
				@include r(1024) {
					margin-left: 10px; } } } }

	&_title_in {
		width: 100%;
		margin: 15px 0;
		text-align: center;
		font-family: 'PlayfairDisplay';
		font-style: italic; }

	&_title {
		font-weight: 400;
		font-size: 50px; }

	&_sub {
		font-size: 24px;
		color: #fff; }

	.products {
		width: calc(100% - 50px);
		position: relative;
		margin-right: 50px;
		background-color: #fff;
		background-size: contain;
		background-repeat: no-repeat;
		box-shadow: 5px 5px 25px 3px #72706F;
		@include clr;
		@include r(768) {
			width: 100%;
			margin-right: 0; }
		&_slider {
			max-height: 100%;
			position: relative;
			z-index: 100000;

			&-nav {
				width: 80px;
				position: absolute;
				top: 60px;
				right: -50px;
				background: #F2D800;
				z-index: 100000;
				@include r(768) {
					top: 0;
					right: 0; } } }


		&_counter {
			position: relative;
			top: 50px;
			padding: 5px 10px;
			background: #000;
			color: #fff;
			text-align: center;
			z-index: 50;

			&_current {
				line-height: 40px; }

			&_total {
				line-height: 40px; } }

		.product {
			display: flex;
			justify-content: space-around;
			height: 100%;
			max-height: 100%;
			position: relative;
			@include r(768) {
				flex-direction: column; }

			&_left {
				display: flex;
				align-items: center;
				flex-basis: 45%;
				position: relative;
				@include r(768) {
					flex-direction: column;
					align-items: center; } }

			&_photo {
				width: 70%;
				@include r(768) {
					width: 90%; } }

			&_views {
				margin-left: auto;
				@include r(768) {
					margin-left: 0; }
				& img {
					max-width: 100%;
					height: auto;
					max-height: 100%;
					position: relative;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%); } }

			&_view {
				width: 66px;
				height: 66px;
				margin-bottom: 10px;
				background-size: contain;
				background-position: center;
				background-repeat: no-repeat;
				border: 2px solid #ccc;
				cursor: pointer;
				@include r(768) {
					display: inline-block;
					margin: 0 10px; }
				@include r(480) {
					margin: 0 5px; }
				&:hover {
					border: 4px solid $blue; } }

			&_right {
				flex-basis: 45%;
				max-width: 500px;
				height: 100%;
				padding: 30px 20px; }

			.rate {
				margin: 10px 0 43px;
				vertical-align: middle;

				.icon {
					font-size: 14px; }

				&_stars {
					display: inline-block;
					vertical-align: middle; }

				&_reviews {
					text-align: center;
					text-decoration: none;
					cursor: pointer;
					color: gray;
					font-weight: bold;
					font-size: 12px;
					line-height: 100%; } }

			&_title {
				font-size: rem(20);
				font-weight: bold; }

			&_info {
				height: 100%; }

			&_nav {
				display: flex;
				width: 100%;
				justify-content: space-between;
				margin-bottom: 28px;
				border-bottom: 2px solid $lgray;
				@include clr;
				@include r(1024) {
					flex-direction: column;
					justify-content: flex-start; }
				&-item {
					flex-basis: 33%;
					height: 100%;
					padding-bottom: 8px;
					text-align: center;
					@include r(1024) {
						padding: 10px 0;
						text-align: left;
						&:not(:last-child) {
							border-bottom: 2px solid $lgray; } }
					& a {
						padding-bottom: 12px;
						color: $gray;
						font-family: $rale;
						font-weight: bold;
						text-decoration: none;
						text-transform: uppercase;
						&:hover {
							color: black;
							border-bottom: 5px solid $blue; } } } }

			&_textual {
				margin-bottom: 20px;
				z-index: 10; }

			&_text {
				font-family: $rale;
				margin-bottom: 40px;
				max-height: 170px;
				overflow: auto;
				z-index: 10;
				cursor: pointer; }

			&_price {
				margin-right: 30px;
				font-weight: bold; }

			&_viewall {
				margin-top: 15%; } } } }

// --------------ABOUT US SECTION--------------------

.about {
	display: flex;
	min-height: 1000px;
	justify-content: space-between;
	align-items: flex-end;
	margin-top: 140px;
	background-position: 50% 0;
	@include clr;
	@include r(768) {
		flex-wrap: wrap;
		justify-content: center;
		padding: 0 20px; }

	&_title {
		@include center(h);
		width: 100%;
		top: -150px;
		@include r(480) {
			top: -60px;
			font-size: rem(80); } }

	&_descr {
		flex-basis: 40%;
		padding: 100px 130px;
		background: #fff;
		@include clr;
		@include r(1300) {
			flex-basis: 60%; }
		@include r(768) {
			flex-basis: 100%;
			align-self: center; }
		@include r(480) {
			padding: 10px; }

		&-wrap {
			width: 100%; }

		&_title {
			text-align: center;
			text-transform: uppercase;
			font-size: 18px;
			font-weight: 600; }

		&_text {
			font-family: $playFair;
			font-weight: 300;
			font-size: 22px;
			text-align: left;
			margin-bottom: 25px; } }

	.icon {
		display: block;
		margin: 15px auto;
		font-size: 13px; }

	&_video {
		width: 30%;
		position: relative;
		padding-bottom: 14.25%;
		padding-top: 30px;
		margin-bottom: 100px;
		height: 0;
		overflow: hidden;
		box-shadow: 0 7px 75px 12px rgba(0, 0, 0, 0.3);
		@include r(768) {
			width: 80%;
			align-self: flex-end;
			margin-bottom: 20px;
			padding-bottom: 41.25%;
			padding-top: 40px; }

		& .icon {
			font-size: 140px;
			position: absolute;
			@include vcenter;
			z-index: 50; }

		& iframe {
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;
			max-height: 100%; }

		&-preview {
			width: 118%;
			height: 108%;
			background-size: cover;
			background-repeat: no-repeat;
			position: absolute;
			top: -8px;
			left: -25px;
			z-index: 30; } } }

// --------------TEAM SECTION--------------------

.team {
	padding-top: 97px;
	padding-bottom: 180px;

	&_title {
		width: 100%;
		font-family: $mons;
		top: -140px;
		@include center(h);
		@include r(1024) {
			top: -120px;
			font-size: rem(140); }
		@include r(768) {
			top: -10px;
			font-size: rem(90); }
		@include r(768) {
			top: 0;
			font-size: rem(60); } }

	.button {
		width: 195px;
		margin: 0 auto; } }

.team_slider {
	width: calc(100% - 50px);
	margin: 0 auto;
	padding-bottom: 125px;
	position: relative;
	@include r(768) {
		width: 100%; } }

.team_slider .slick-list {
	width: 100%;
	height: 385px;
	position: relative;
	z-index: 1;
	margin: 0 auto;
	background: rgba(0, 0, 0, 0);
	outline: none;
	@include r(768) {
		height: auto;
		padding: 50px 0; }
	& .slick-slide {
		& > div {
			@include r(768) {
				width: 90%;
				margin: 0 auto; } } } }

.team_slider .slick-track {
	opacity: 0; }

.team_slider .slick-slide:nth-child(even) {
	margin-top: 30px;
	position: relative;
	left: -20px;
	z-index: 30;
	@include r(768) {
		left: 0; }
	@include r(480) {
		margin-top: 0; } }

.team_slider .slick-slide:nth-child(4n+1) .member {
	left: 35px;
	@include r(768) {
		left: 0; } }

.team_slider div:nth-child(odd) .member {
	position: relative;
	left: -20px;
	@include r(768) {
		left: 0; } }

.team_slider .slick-slide:nth-child(4n+2) {
	left: 20px;
	@include r(768) {
		left: 0; } }

.team_slider .slick-arrow {
	width: 50px;
	height: 50px;
	top: 33%;
	opacity: 1;
	background: #fff;
	border-radius: 50%;
	box-shadow: 0 5px 15px 10px rgba(0, 0, 0, 0.1);

	&:before {
		display: block;
		position: absolute;
		top: 45%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: $arrowBlue;
		font-size: 40px;
		line-height: 40px;
		vertical-align: middle; } }

.team_slider .slick-next {
		right: -25px;
		z-index: 10000;
		&:before {
			content: '\FFEB'; }
		@include r(768) {
			right: 0; } }

.team_slider .slick-prev {
		left: -25px;
		z-index: 10000;
		&:before {
			content: '\FFE9'; }
		@include r(768) {
			left: 0; } }

.member {
	padding: 28px 10px 45px;
	background: #fff;
	text-align: center;
	font-size: rem(16);
	border-radius: 5px;
	box-shadow: 0 12px 15px 10px rgba(0, 0, 0, 0.1);
	@include r(768) {
		font-size: rem(16);
		width: 90%; }
	&:hover {
		background: $blue;
		cursor: pointer;
		& p, h3 {
			color: #fff; } }

	&_photo {
		display: inline-block;
		margin: 0 auto;
		width: 100px;
		height: 100px;
		border-radius: 50%;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain; }

	&_name {
		font-family: $rale;
		color: $blue;
		margin-bottom: 10px; }

	&_inst {
		color: $dgray; }

	.icon {
		font-size: 6px; }

	&_city {
		display: inline;
		color: $dgray; } }

// --------------LATEST UPDATES--------------------

.updates {
	margin-bottom: 40px;

	&_title {
		display: block;
		text-align: center;
		font-family: $mons;
		font-size: 18px;
		text-transform: uppercase;
		letter-spacing: 3px; }

	& .icon {
		display: block;
		margin: 15px auto;
		font-size: 13px; }

	&_wrap__in {
		display: flex;
		width: 100%;
		justify-content: space-between;
		@include r(768) {
			flex-direction: column;
			align-items: center; } } }
.update {
	flex-basis: 30%;
	text-align: center;
	cursor: pointer;
	@include r(768) {
		width: 50%;
		margin-bottom: 20px; }
	@include r(480) {
		width: 100%; }

	& img {
		width: 100%;
		height: auto; }

	&:hover {
		box-shadow: 0 12px 25px 10px rgba(0, 0, 0, 0.1); }

	&:hover> &_descr {
		color: $dblue;
		background: #fff; }

	&_photo {
		width: 100%;
		height: 200px;
		background-repeat: no-repeat;
		background-position: 50% 0;
		background-size: cover; }

	&_descr {
		padding: 25px;
		font-size: 16px;
		color: $dgray;
		font-family: $rale; } }

// --------------SECTION SOCIAL--------------------

.social {
	padding: 57px 0 60px;
	@include clr;

	&::after {
		display: none; }

	.title_social {
		position: absolute;
		top: -50px;
		left: 0;
		max-width: 100%;
		font-family: $mons;
		font-size: rem(70);
		line-height: 1;
		cursor: default;
		@include r(768) {
			font-size: rem(40); } } }

.inst {
	display: inline-block;
	width: 40%;
	vertical-align: middle;
	margin-right: 20px;
	background: #fff;
	@include r(840) {
		width: 100%;
		display: block; }

	.ocean {
		width: 100%;
		font-size: 0;
		& img {
			width: 50%; } }

	&_meta {
		padding: 20px;

		.icon {
			vertical-align: middle;
			font-size: 30px;
			fill: $blue; } }

	&_follow {
		margin: 0 20px;
		text-decoration: none;
		text-transform: uppercase;
		vertical-align: middle;
		font-family: $roboCon;
		font-size: 24px;
		font-weight: 600;
		color: $blue;
		line-height: 1; }

	&_name {
		display: inline-block;
		vertical-align: middle;
		font-family: $rale; } }

.fb {
	display: inline-block;
	width: 440px;
	padding: 30px 50px;
	margin-bottom: 60px;
	float: right;
	background: linear-gradient( #385490 50%, #E5E5E5 50%);
	background-repeat: no-repeat;
	text-align: center;
	vertical-align: top;
	@include clr;
	@include r(840) {
		display: block;
		margin: 0 auto;
		float: none; }
	@include r(480) {
		width: 100%;
		max-width: 100%; }

	& > .icon {
		display: inline-block;
		margin-bottom: 20px;
		font-size: 35px;
		fill: #F0F0F0; }

	&_likebox {
		height: 130px;
		padding: 10px;
		position: relative;
		text-align: left;
		@include cover; }

	&_profile-photo {
		display: inline-block;
		width: 55px;
		height: 55px;
		padding: 12px 5px;
		background: $blue;
		border: 2px solid #fff;
		color: #fff;
		text-align: center;
		text-transform: uppercase;
		line-height: 1.2;
		font-weight: 600;
		font-size: 12px;
		cursor: default; }

	&_company-title__in {
		display: inline-block;
		width: 80%;
		color: #fff;
		& h3 {
			font-size: 18px;
			line-height: 1; }
		& p {
			font-size: 12px;
			font-weight: 600; } }

	&_button {
		display: inline-block;
		position: absolute;
		bottom: 10px;
		padding: 5px 5px 5px 8px;
		background: #F6F7F9;
		text-align: center;
		text-decoration: none;
		cursor: pointer;
		font-size: 12px;
		color: #000;
		font-weight: 600;
		line-height: 1;

		& .icon {
			margin-right: 5px;
			font-size: 13px;
			vertical-align: bottom; } }

	.like {
		left: 10px; }

	.share {
		right: 10px; }

	&_friends {
		border: 10px solid #F6F7F9;
		background: #fff;
		padding: 10px 8px 8px;
		text-align: left;

		&-textual {
			font-size: 12px;
			font-weight: 600; } }

	&_users-list {
		margin-top: 7px;
		@include clr;

		& li {
			width: 32px;
			height: 32px;
			float: left;
			margin-right: 2px;
			&:last-child {
				margin: 0; } } }

	&_user {
		display: block;
		width: 100%;
		height: 100%;
		@include cover;
		cursor: pointer; } }

#icon-waves>image {
	fill: #fff!important; }

// --------------Footer--------------------

.footer {
	height: 420px;
	padding-bottom: 20px;
	background: #000;
	color: #fff;

	.container {
		display: flex;
		justify-content: flex-end;
		flex-wrap: wrap;
		@include r(768) {
			justify-content: center; } }

	&_rights {
		width: 100%;
		align-self: flex-end;
		color: $footergray;
		& span {
			margin-right: 30px;
			font-family: $rale;
			font-size: 12px; } }

	&_right {
		width: 430px;
		position: relative;
		max-height: 100%;
		padding-top: 25px; }

	&_nav {
		width: 300px;
		@include clr;
		@include r(768) {
			width: 100%; }

		&-item {
			width: 45%;
			float: left;
			font-size: 14px;
			&:nth-child(odd) {
				margin-right: 10%; }
			& a {
				text-decoration: none;
				color: #fff;
				font-family: $rale;
				cursor: pointer; }
			& .icon {
				margin-right: 7px;
				vertical-align: bottom; } } }

	&_subscr {
		position: absolute;
		bottom: 0;

		&-title {
			font-size: rem(16); }

		&-form {
			border-bottom: 2px solid $footergray; }

		& .icon-footer_mail {
			font-size: 12px;
			margin-right: 10px; } } }


#footer_email {
	background: transparent;
	color: #fff; }

.mail_submit {
	background: none;
	& .icon {
		fill: #fff;
		color: $blue; } }
